<template>
    <div>
        <el-row class="top-bar" type="flex" justify="space-between">
            <el-col :span="4" :lg="3" :xs="5">
                <div class="logo grid-content">至尊宝</div>
            </el-col>
            <el-col class="user-info" :span="8">
                <el-button type="text" size="mini" @click="toNews()">
                    消息
                    <el-badge is-dot class="news-btn icon icon-remind"></el-badge>
                </el-button>
                <span class="user-name grid-content">{{userName}}</span>
                <el-button type="text" @click="quit()">退出</el-button>
            </el-col>
        </el-row>
        <el-row>
            <el-col tag="aside" :span="3" :lg="3"class="side-bar">
                <el-menu router unique-opened :default-active="activeIndex" class="el-menu-vertical-demo">

                    <el-submenu index="index" v-if="power && power.checkList1.length || false">
                        <template slot="title">运营信息</template>
                        <el-menu-item index="index" :active="true"  v-if="checkListPower('checkList1', '首页轮播')">首页轮播</el-menu-item>
                        <el-menu-item index="system-push" v-if="checkListPower('checkList1', '系统推送')">系统推送</el-menu-item>
                        <el-menu-item index="operating-articles" v-if="checkListPower('checkList1', '运营文章')">运营文章</el-menu-item>
                        <el-menu-item index="vehicle-manage" v-if="checkListPower('checkList1', '车型库管理')">车型库管理</el-menu-item>
                    </el-submenu>

                    <el-submenu index="2" v-if="power && power.checkList2.length || false">
                        <template slot="title">用户</template>
                        <el-submenu index="user-info">
                          <template slot="title">用户信息</template>
                          <el-menu-item index="user-info" v-if="checkListPower('checkList2', '用户信息')">用户信息</el-menu-item>
                          <el-menu-item index="car-owner-info" v-if="checkListPower('checkList2', '车东信息')">车东信息</el-menu-item>
                        </el-submenu>
                        <el-menu-item index="vehicle-info" v-if="checkListPower('checkList2', '车辆信息')">车辆信息</el-menu-item>
                        <el-menu-item index="ticket-info" v-if="checkListPower('checkList2', '罚单信息')">罚单信息</el-menu-item>
                        <el-menu-item index="user-credit" v-if="checkListPower('checkList2', '用户信用')">用户信用</el-menu-item>
                    </el-submenu>

                    <el-menu-item index="order" v-if="power && power.checkList3.length || false">订单</el-menu-item>

                    <el-submenu index="4" v-if="power && power.checkList4.length || false">
                        <template slot="title">财务</template>
                        <!-- <el-menu-item index="user-account">用户账户</el-menu-item> -->
                        <!-- <el-menu-item index="user-account-income-expense">用户账户收支明细</el-menu-item> -->
                        <el-menu-item index="platform-account" v-if="checkListPower('checkList4', '平台账户')">平台账户</el-menu-item>
                        <el-menu-item index="withdraw" v-if="checkListPower('checkList4', '提现管理')">提现管理</el-menu-item>
                    </el-submenu>

                    <el-submenu index="5" v-if="power && power.checkList5.length || false">
                        <template slot="title">系统设置</template>
                        <el-menu-item index="account-manage" v-if="checkListPower('checkList5', '账户管理')">账户管理</el-menu-item>
                        <el-menu-item index="my-account" v-if="checkListPower('checkList5', '我的账户')">我的账户</el-menu-item>
                    </el-submenu>
                    <!-- <el-menu-item index="news">消息</el-menu-item> -->
                </el-menu>
            </el-col>
            <el-col :span="19" :lg="20" id="main">
                <router-view :API="API"></router-view>
            </el-col>
        </el-row>
    </div>
</template>

<script>
export default {
    data() {
        return {
            userName: JSON.parse(sessionStorage.getItem('zhizunbao_login')).userName,
        }
    },
    computed: {
        // 导航高亮
        activeIndex() {
            return this.$store.state.activeIndex
        },
        power(){
            return JSON.parse(sessionStorage.getItem('power'))
        }
    },
    props: ['API'],
    methods: {
        quit() {
            this.$confirm('是否退出?', '提示', {type: 'warning'})
                .then(() => {
                    sessionStorage.removeItem('zhizunbao_login')
                    this.userName = ''
                    this.$message({
                        message: "退出成功",
                        type: 'success'
                    })
                    this.$router.push('/')
                })
                .catch(() => {})
        },
        toNews() {
            this.$router.push('/news')
        },
        checkListPower(list, item) {
            return this.power && this.power[list].includes(item) || false
        }
    },
    mounted(){

    }
}
</script>

<style lang="scss">
.news-btn {
    font-size: 18px;
}
.el-badge__content.is-dot {
    position: absolute;
    top: -3px;
    top: -99999px;
}
</style>



